#footer, .fc-toolbar .ui-button, .fileinput .thumbnail {
	text-align: center;
}
.four-zero {
	text-align: center;
	footer>a {
		text-align: center;
	}
}
.ie-warning, .login-content, .login-navigation {
	text-align: center;
}
.pt-inner {
	text-align: center;
	.pti-footer>a {
		text-align: center;
	}
}
.top-footer {
	width: 100%;
	line-height: 1.25rem;
	opacity: 1;
	transform: translate(0px, 0px);
	font-size: 14px;
	flex: 0 0 auto;
	h6 {
		font-size: 14px;
		margin-bottom: 1rem;
		font-weight: 600;
	}
	p {
		color: #9493a9;
	}
	a {
		color: #9493a9;
		line-height: 2;
		color: #9493a9;
		line-height: 2;
		address {
			color: #9493a9;
			margin-bottom: 0;
			line-height: 2;
		}
	}
	img {
		width: 80px;
		border: 1px solid #e6eaea;
		padding: 2px;
		height: 80px;
		&:hover {
			color: #8e9090;
			opacity: .6;
		}
	}
	.list-unstyled li {
		border-bottom: 0;
	}
}
.footer-payments {
	padding-left: 0;
	list-style: none;
	margin: 0;
	line-height: 1.25rem;
	li {
		&:not(:last-child) {
			margin-right: 1.5rem;
		}
		display: inline-block;
	}
	a {
		color: #a7a8c7;
		text-decoration: none;
		font-size: 2rem;
		&:hover {
			opacity: .9;
		}
	}
}
.main-footer {
	padding: 20px 30px 20px 280px;
	font-size: 14px;
	font-weight: 400;
	background-color: $white;
	border-top: 1px solid #e6eaea;
}
.footer-social-list {
	li {
		&:not(:last-child) {
			margin-right: 1.5rem;
		}
		display: inline-block;
	}
	a {
		color: #9493a9;
		text-decoration: none;
		font-size: 1rem;
	}
	padding-left: 0;
	list-style: none;
	margin: 0;
	line-height: 1.25rem;
}