@charset "UTF-8";
.page-main {
	position: relative;
	overflow: hidden;
	&:after {
		content: "";
		height: 280px;
		position: absolute;
		z-index: -1;
		width: 100%;
		top: 0;
		box-shadow: 0 5px 20px $black-2;
		overflow: hidden;
	}
}
.dropdown-menu-arrow:before {
	content: "";
	position: absolute;
	right: 11px;
	top: -10px;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 0 10px 10px 10px;
	border-color: transparent transparent $white transparent;
	z-index: 9999;
}
