			.cbp_tmtime span:last-child {
				font-size: 13px;
			}
			.cbp_tmlabel {
				background: $white;
				&:after {
					border-right-color: $white;
				}
			}
		}
		.empty span {
			color: $color;
		}
		.cbp_tmtime {
			display: block;
			width: 21%;
			padding-right: 70px;
			position: absolute;
			span {
